.menu-icon{
  -webkit-transition: all .3s;
  transition: all .3s;
}
.rotate-icon{
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}
.menu-item span{
  display: inline-block;
  overflow: hidden;
  width: 69px;
  text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: bottom;
  -webkit-transition: width .2s ease .2s;
  transition: width .2s ease .2s;
}
.menu-item i{
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
  -webkit-transition: font-size .2s ease, -webkit-transform .2s ease;
  transition: font-size .2s ease, -webkit-transform .2s ease;
  transition: font-size .2s ease, transform .2s ease;
  transition: font-size .2s ease, transform .2s ease, -webkit-transform .2s ease;
  vertical-align: middle;
  font-size: 16px;
}
.collapsed-menu span{
  width: 0px;
  -webkit-transition: width .2s ease;
  transition: width .2s ease;
}
.collapsed-menu i{
  -webkit-transform: translateX(5px);
          transform: translateX(5px);
  -webkit-transition: font-size .2s ease .2s, -webkit-transform .2s ease .2s;
  transition: font-size .2s ease .2s, -webkit-transform .2s ease .2s;
  transition: font-size .2s ease .2s, transform .2s ease .2s;
  transition: font-size .2s ease .2s, transform .2s ease .2s, -webkit-transform .2s ease .2s;
  vertical-align: middle;
  font-size: 22px;
}
